.dialog_imagesContainer__XXYqs {
	padding: 0px !important;
	position: relative;
}

.dialog_imagesContainer__XXYqs i {
	position: absolute;
	height: 80px;
	width: 80px;
	bottom: 0px;
	left: 0px;
	color: white;
	background-color: black;
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	transition-duration: 200ms;
}
.dialog_imagesContainer__XXYqs i:nth-child(2) {
	left: 80px !important;
}

.dialog_imagesContainer__XXYqs i:hover {
	background-color: rgba(0, 0, 0, 0.9);
}

.dialog_imagesContainer__XXYqs img {
	width: 100%;
	height: 100%;
	min-height: 400px;
	max-height: 800px;
	object-fit: cover;
	border-top-left-radius: 4px;
	border-bottom-left-radius: 4px;
}

.dialog_projectDetails__3RMIN {
	padding: 32px;
	padding-top: 40px;
	display: flex;
	position: relative;
	flex-direction: column;
}

.dialog_projectDetails__3RMIN .dialog_exit__2aTIM {
	position: absolute;
	top: 24px;
	right: 24px;
	transition-duration: 400ms;
	cursor: pointer;
}

.dialog_projectDetails__3RMIN .dialog_exit__2aTIM:hover {
	-webkit-transform: rotateZ(90deg);
	        transform: rotateZ(90deg);
}

.dialog_time__zeW3h {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.dialog_time__zeW3h span {
	color: #888;
	margin-left: 8px;
	font-weight: normal;
	text-transform: uppercase;
}

.dialog_description__2qtJl {
	color: #333;
}

.dialog_roles__3HUcS {
	display: flex;
	flex-direction: row;
	margin: 16px 0px;
}
.dialog_role__1CWPp {
	padding: 4px 8px;
	background-color: #eee;
	color: #888;
	border-radius: 4px;
	margin-right: 16px;
	font-size: 12px;
}

h4 {
	margin: 32px 0px 16px 0px;
}

.dialog_techs__3k6uk {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	margin-top: 4px;
}

.dialog_tech__J8Kh9 {
	padding: 8px 16px;
	margin-right: 8px;
	margin-bottom: 8px;
	list-style: none;
	background-color: #eee;
	color: #888;
	border-radius: 4px;
	font-size: 13px;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

@-webkit-keyframes dialog_openWrapper__YmeYD {
	0% {
		opacity: 0;
	}
}

@keyframes dialog_openWrapper__YmeYD {
	0% {
		opacity: 0;
	}
}

@-webkit-keyframes dialog_openDialog__3Iejl {
	0% {
		opacity: 5;
		-webkit-transform: translateY(30px);
		        transform: translateY(30px);
	}
}

@keyframes dialog_openDialog__3Iejl {
	0% {
		opacity: 5;
		-webkit-transform: translateY(30px);
		        transform: translateY(30px);
	}
}

.dialog_siteUrl__37pbT {
	background-color: blueviolet;
	color: white;
	text-decoration: none;
	border-radius: 100px;
	padding: 16px 32px;
	margin: auto;
	margin-top: 40px;
}

.dialog_siteUrl__37pbT:hover {
	color: white;
}

@media only screen and (max-width: 600px) {
	.dialog_dialogContainer__2t0-c {
		display: block !important;
	}
	.dialog_time__zeW3h {
		flex-direction: column;
	}
	.dialog_imagesContainer__XXYqs img {
		border-radius: 0px;
	}
}

