.dialogWrapper {
	position: fixed;
	left: 0px;
	top: 0px;
	overflow: auto;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100vw;
	background-color: rgba(0, 0, 0, 0.4);
	animation: openWrapper 100ms;
}

.dialogContainer {
	width: min(100vw, 1024px);
	height: fit-content;
	margin: auto;
	background-color: white;
	border-radius: 4px;
	box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.4);
	padding: 0px !important;
	animation-delay: 100ms;
	animation: openDialog 400ms;
}

.imagesContainer {
	padding: 0px !important;
	position: relative;
}

.imagesContainer i {
	position: absolute;
	height: 80px;
	width: 80px;
	bottom: 0px;
	left: 0px;
	color: white;
	background-color: black;
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	transition-duration: 200ms;
}
.imagesContainer i:nth-child(2) {
	left: 80px !important;
}

.imagesContainer i:hover {
	background-color: rgba(0, 0, 0, 0.9);
}

.imagesContainer img {
	width: 100%;
	height: 100%;
	min-height: 400px;
	max-height: 800px;
	object-fit: cover;
	border-top-left-radius: 4px;
	border-bottom-left-radius: 4px;
}

.projectDetails {
	padding: 32px;
	padding-top: 40px;
	display: flex;
	position: relative;
	flex-direction: column;
}

.projectDetails .exit {
	position: absolute;
	top: 24px;
	right: 24px;
	transition-duration: 400ms;
	cursor: pointer;
}

.projectDetails .exit:hover {
	transform: rotateZ(90deg);
}

.time {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.time span {
	color: #888;
	margin-left: 8px;
	font-weight: normal;
	text-transform: uppercase;
}

.description {
	color: #333;
}

.roles {
	display: flex;
	flex-direction: row;
	margin: 16px 0px;
}
.role {
	padding: 4px 8px;
	background-color: #eee;
	color: #888;
	border-radius: 4px;
	margin-right: 16px;
	font-size: 12px;
}

h4 {
	margin: 32px 0px 16px 0px;
}

.techs {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	margin-top: 4px;
}

.tech {
	padding: 8px 16px;
	margin-right: 8px;
	margin-bottom: 8px;
	list-style: none;
	background-color: #eee;
	color: #888;
	border-radius: 4px;
	font-size: 13px;
	width: fit-content;
}

@keyframes openWrapper {
	0% {
		opacity: 0;
	}
}

@keyframes openDialog {
	0% {
		opacity: 5;
		transform: translateY(30px);
	}
}

.siteUrl {
	background-color: blueviolet;
	color: white;
	text-decoration: none;
	border-radius: 100px;
	padding: 16px 32px;
	margin: auto;
	margin-top: 40px;
}

.siteUrl:hover {
	color: white;
}

@media only screen and (max-width: 600px) {
	.dialogContainer {
		display: block !important;
	}
	.time {
		flex-direction: column;
	}
	.imagesContainer img {
		border-radius: 0px;
	}
}
