@import url("https://fonts.googleapis.com/css2?family=Cairo&display=swap");
* {
	font-family: "Cairo";
}

.App {
	position: relative;
}

button {
	transition-duration: 200ms;
}

button:active {
	transform: scale(0.9);
	opacity: 0.9;
}

nav {
	display: flex;
	flex-direction: row;
	align-items: center;
	height: 100px;
	padding: 0px 20px;
}

nav img {
	height: 80px;
	margin-right: auto;
}

.introduction {
	display: flex;
	flex-direction: column;
	width: 90%;
	margin: 0px auto;
	justify-content: center !important;
	align-items: center !important;
	min-height: calc(100vh - 100px);
	background-image: url(./assets/img/hero.svg);
	background-repeat: no-repeat;
	background-position: bottom right;
	background-size: min(90%, 500px);
	text-align: center;
}

.introduction img {
	width: 150px;
	height: 150px;
	border-radius: 80px;
	margin: 20px;
	object-fit: cover;
}

.introduction h1 {
	font-size: 42px;
	text-align: center;
}

.introduction p {
	font-size: 17px;
}

.introduction button a {
	color: unset;
	text-decoration: none;
}
.introduction button a:hover {
	color: white;
}

.blueButton {
	height: 50px;
	padding: 0px 20px;
	border: none;
	color: white;
	background-color: #7510f7;
	border-radius: 30px;
	outline: none !important;
	font-weight: bold;
}

.aboutContent {
	height: fit-content;
	width: 100%;
	padding: 100px 0px;
	background-color: #7510f7;
	color: white;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.aboutContent * {
	width: min(90%, 600px);
}

.skills {
	max-width: min(90%, 1300px);
	background-color: white;
	border-radius: 20px;
	box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.3);
	border: 1px solid #dddddd;
	margin: -50px auto 100px auto !important;
}

.bCard {
	width: 100%;
	display: flex;
	text-align: center;
	flex-direction: column;
	align-items: center;
	padding: 40px 20px;
	height: 100%;
}

.bCard p {
	color: rgb(75, 75, 75);
	margin: 10px 0px;
}

.bCard img {
	margin: 20px 0px;
}

.title {
	color: #7510f7;
	margin: 30px 0px;
}

.bCard .list {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.bCard .items {
	padding: 10px 0px;
}

.skills .col-lg-4:nth-child(1) .bCard,
.skills .col-lg-4:nth-child(2) .bCard {
	border-right: 1px solid #dddddd;
}

#footer {
	padding: 40px;
	padding-top: 100px;
	width: 100%;
	background-color: #7510f7;
	color: #eeeeee;
	display: flex !important;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

#footer .links {
	margin: 20px 0px;
}

#footer i {
	color: white;
	font-size: 25px;
	padding: 0px 10px;
}

#footer img {
	height: 100px;
}

.mContainer {
	width: 80%;
	margin: 40px auto 0px auto !important;
	position: relative;
	background-color: #141c38;
	color: white;
	padding: 0px !important;
	border-radius: 20px;
	margin-bottom: -100px !important;
	height: fit-content !important;
	background-image: url(./assets/img/bg-boost-desktop.svg);
	background-position: center;
	background-size: cover;
}

.mContainer .col {
	display: flex;
	height: 160px;
	justify-content: center;
	align-items: center;
}

.mContainer a {
	text-decoration: none;
	background-color: transparent;
	border-radius: 40px;
	border: 2px solid #7510f7;
	color: white;
	padding: 1.25em 2em;
	font-weight: 400;
	border-width: 2px;
}

.projectsContainer {
	max-width: min(90%, 1300px);
	display: flex;
	flex-direction: column;
	margin: 10vh auto;
}

.projectsContainer h1 {
	margin: 10vh auto 0px auto;
	font-weight: "bolder";
}

.projectCard {
	position: relative;
	margin-bottom: 20px;
	transition-duration: 200ms;
}
.projectCard img {
	width: 100%;
	height: 250px;
	border-radius: 4px;
	transition-duration: 200ms;
	object-fit: cover;
}

.projectCard:hover {
	box-shadow: 2px 2px 3px #dddddd;
}

.projectDetail {
	position: absolute;
	height: 0px;
	overflow: hidden;
	bottom: 0px;
	width: 100%;
	background-color: #141c3ac5;
	color: white;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	transition-duration: 200ms;
	padding: 0px 20px;
	text-align: center;
	font-weight: bold;
	font-size: 17px;
	border-radius: 4px;
}
.projectCard button {
	border: 1px solid #fff;
	padding: 8px 16px;
	color: white;
	background: transparent;
	margin-top: 8px;
	border-radius: 4px;
	font-size: 15px;
}

.projectCard:hover .projectDetail {
	height: 250px;
}

@media only screen and (max-width: 600px) {
	.bCard {
		border: none !important;
	}
}
